import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import jwtDecode from 'jwt-decode'

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
      {
        path: '/',
        name: 'home',
        component: Home,
        meta: {requiresAuth: true}
      },
      {
        path: '/login',
        name: 'login',
        component: () => import('./views/Login.vue')
      },
      {
        path: '/dashboard',
        name: 'dashboard',
        component: () => import('./views/Dashboard.vue'),
        redirect: '/dashboard/users',
        meta: {
          requiresAuth: true,
          roles:['admin']
        },
        children:[{
          path: 'users',
          name: 'users',
          component: () => import('./views/UsersShow.vue'),
          meta: {
            requiresAuth: true,
            roles:['admin']
          }
        },
        {
          path: 'configs',
          name: 'configs',
          component: () => import('./views/ConfigsShow.vue'),
          meta: {
            requiresAuth: true,
            roles:['admin']
          }
        },
        {
          path: 'filetypes',
          name: 'filetypes',
          component: () => import('./views/TypeOfFilesShow.vue'),
          meta: {
            requiresAuth: true,
            roles:['admin']
          }
        },
        {
          path: 'roles',
          name: 'roles',
          component: () => import('./views/RolesShow.vue'),
          meta: {
            requiresAuth: true,
            roles:['admin']
          }
        },
        {
          path: 'marks',
          name: 'marks',
          component: () => import('./views/MarksShow.vue'),
          meta: {
            requiresAuth: true,
            roles:['admin']
          }
        }
      ]
      },
      {
        path: '/account',
        name: 'account',
        component: () => import('./views/Account.vue'),
        meta: {requiresAuth: true}
      },
      {
        path: '/commit/new',
        name: 'addCommit',
        component: () => import('./views/CommitAdd.vue'),
        meta: {
          requiresAuth: true,
          roles:['user']
        }
      },
      {
        path: '/commit/:id',
        name: 'commit',
        component: () => import('./views/Commit.vue'),
        meta: {requiresAuth: true}
      },
      {
        path: '/help',
        name: 'help',
        component: () => import('./views/HelpPage.vue')
      },
      {
        path: '/forbidden',
        name: 'forbidden',
        component: () => import('./views/ForbiddenPage.vue')
      },
      {
        path: '/servererror',
        name: 'servererror',
        component: () => import('./views/ServerErrorPage.vue')
      },
      {
        path: '/badrequest',
        name: 'badrequest',
        component: () => import('./views/BadRequestPage.vue')
      },
      {
        path: '/notfound',
        name: 'notfound',
        component: () => import('./views/NotFoundPage.vue')
      },
      {
        path: '*',
        redirect: '/notfound'
      }
    ]
  })

  router.beforeEach(async (to, from, next) => {                //   
    if(!to.matched.some(record => record.meta.requiresAuth))   //   
      return next();                                           //  
    const token = localStorage.getItem('token')
    if ( token == null) {                                     //    localStorage
      return next({
        path: '/login'                                        //   
      })
    } 
    const dtoken = jwtDecode(localStorage.token);             // 
    const expDate = new Date(dtoken.exp * 1000)               //  
    if(expDate < new Date()) {                                //     
      return next({                                           //  
        path: '/login',                                       //   
        query: { redirect: to.fullPath }                      //  
      })
    }
    if(!to.meta.roles)                                        //     
      return next()                                           //  
    if(to.meta.roles.includes('admin')) {                     //    
      if(dtoken.role.roleName == "Admin")                                   // 
        return next()                                         //  
      else                                                    //
        return next({path: '/forbidden'})                     //   
    }
    if(to.meta.roles.includes('adminABD')) {                  //    
      if(dtoken.role.roleName == "Admin ABD" )                    // 
        return next()                                         //  
      else                                                    //
        return next({path: '/forbidden'})                     //   
    }
    if(to.meta.roles.includes('user')) {                      //    
      if(dtoken.role.roleName == "User")                      // 
        return next()                                         //  
      else                                                    //
        return next({path: '/forbidden'})                     //   
    } 
  })

  export default router;